import { ImagePreview, Toast } from 'vant';
import apiUrl from '@/api/apiUrl'
import httpService from '@/api/httpService'
import { util } from '@/utils/util'
import qs from 'qs'


export default {
    data() {
        return {
            images: [
                'https://img.yzcdn.cn/vant/apple-1.jpg',
                'https://img.yzcdn.cn/vant/apple-2.jpg',
                'https://img.yzcdn.cn/vant/apple-3.jpg',
                'https://img.yzcdn.cn/vant/apple-4.jpg',
            ],
            historyList: [],
            loading: false,
            finished: false,
            refreshLoading: false,
            pageSize: 10,
            currentPage: 1,
        }
    },
    created() {
    },
    methods: {
        backHandler() {
            this.$router.go(-1)
        },
        updateList() {
            const opts = {  
                apiObj: apiUrl.findHistoryByGoodsIdAndTimeSort,
                query: {
                    page: this.currentPage - 1,
                    size: this.pageSize,
                    gId: this.$route.query.goodsId,
                    sort: 'DESC',
                }
            }
            httpService.accessAPI(opts)
                .then(res => {
                        if(res.code !== '0'){
                            Toast(res.msg);
                            return;
                        }
                        
                        this.historyList = res.data
                        this.loading = false;
                        this.historyList.forEach(item => {
                            item.bidTime = util.dateToStr(new Date(item.bidTime), 5).split(' ');
                            console.log(item.bidTime);
                        })
                        // this.historyList.forEach(item => {
                        //     item.imgs = item.imgs.split(";");
                        //     console.log(item.imgs[0]);
                        // })
                        console.log(this.historyList);
                        if(this.historyList.length <= this.currentPage * this.pageSize) {
                            this.finished = true;
                        }else {
                            this.currentPage ++
                        }
                    })
        },
        onLoad() {
            this.updateList()
        },
        onRefresh() {
            this.currentPage === 1
            this.updateList()
        },
    }
}



